<template>
  <div class="list-container">
    <van-nav-bar left-arrow left-text="返回" title="搜索列表" @click-left="onClickLeft" fixed />
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getArticleList">
        <van-cell v-for="(item, index) in articleList" :key="index" :title="item.title">
          <template slot="label">
            <div @click="goDetail(item.art_id)">
              <!-- 文章图片 -->
              <van-grid :border="false" :column-num="3">
                <van-grid-item v-for="(imgItem, imgIndex) in item.cover.images" :key="imgIndex">
                  <van-image :src="imgItem" lazy-load />
                </van-grid-item>
              </van-grid>
              <!-- 文章作者信息等 -->
              <div class="intro">
                <span>{{item.aut_name}}</span>
                <span>{{item.pubdate | dateFormat}}</span>
              </div>
            </div>
            <!-- 文章 icon -->
            <van-grid :column-num="3" clickable>
              <van-grid-item @click="comments">
                <template slot="icon">
                  <van-icon name="comment-o" class="func-icon" />
                </template>
              </van-grid-item>
              <van-grid-item icon="like-o">
                <template slot="icon">
                  <van-icon name="like-o" class="func-icon" :info="item.ch_id" />
                </template>
              </van-grid-item>
              <van-grid-item icon="star-o">
                <template slot="icon">
                  <van-icon name="star-o" class="func-icon" />
                </template>
              </van-grid-item>
            </van-grid>
          </template>
        </van-cell>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getSearchArticle } from "@/api/article";
export default {
  data() {
    return {
      articleList: [],
      loading: false,
      finished: false,
      isLoading: false,
      keywords: this.$route.params.keywords,
      page: 0,
      per_page: 10
    };
  },
  methods: {
    onRefresh() {
      this.page++;
      this.articleList = [];
      this.getArticleList();
      this.isLoading = false;
    },
    onClickLeft() {
      this.$router.back();
    },
    async getArticleList() {
      this.page++;
      let res = await getSearchArticle({
        page: this.page,
        per_page: this.per_page,
        q: this.keywords
      });
      this.articleList = [...this.articleList, ...res.results];
      this.loading = false;

      if (this.articleList.length === res.total_count) {
        this.finished = true;
      }
    },
    comments() {
      this.$login();
    },
    goDetail(artId) {
      this.$router.push(`/detail/${artId}`);
    }
  }
};
</script>

<style lang="less" scoped>
.list-container {
  .van-nav-bar {
    .van-nav-bar__text {
      color: #fff !important;
    }
  }
  .van-list {
    margin-top: 46px;
    .intro {
      display: flex;
      justify-content: space-between;
    }
    .func-icon {
      font-size: 16px;
    }
  }
}
</style>